import React from 'react';
import { Container, Text, Card, Title } from '@mantine/core';

const avvisiDB = [
  {titolo: `Uso dell'Email`, data: '08/05/2021', 
  testo: 
  `L’Email studiomedico.ceo@libero.it va usata solo per inviare esami, \
  visite specialistiche e impegnative/ricette. In oggetto inserire la \
  dicitura "esami" o "ricette" in modo che gli esami vengano visti dalla \
  dott.Ceo mentre le ricette vengano predisposte direttamente dalla segretaria. \
  NON RICHIEDERE MALATTIE INPS MEDIANTE EMAIL.`
  },
];

const Avviso = (props) => {
  return (
    <Card shadow='sm' p='lg' radius='lg'>
      <Text weight='bold' size='xl' color='white'>{props.titolo}</Text>
      <Text weight='bold' size='sm' color='gray' pb='md'>{props.data}</Text>
      <Text align='left' size='sm' color='white'>{props.testo}</Text>
    </Card>
  );
}

export default function Avvisi() {
  return (
    <Container>
      <Title order={2} pb='xl' pl='sm' pt='sm' sx={(theme) => ({color: theme.colors.grape[7]})}>Avvisi</Title>
      {
        avvisiDB.map((el, idx) => <Avviso key={idx} titolo={el.titolo} data={el.data} testo={el.testo}/>)
      }
    </Container>
  );
}
